body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #2c2c2e;
  height: 100vh;
  width: 100vw;
}

form {
  display: flex;
  align-items: center;
  justify-content: center;
}

form input {
  outline: 0;
  border: 0;
  padding: 1rem 1.5rem;
  font-size: 18px;
  width: 600 !important;
  height: 24px;
  color: #efeeee;
  border-radius: 50px;
  letter-spacing: 0.00938em;
  font-weight: 400;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.4);
  opacity: 0.7;
  background-color: #2c2c2e;
  transition: all 0.2s ease-in-out;
  text-align: center;
}

input:active,
input:focus,
input:hover {
  opacity: 0.9;
  height: 36px;
}

a {
  opacity: 0.7;
  color: #efeeee;
  text-decoration: none;
}

a:hover {
  opacity: 0.9;
}
